function postErrorMessages() {
  // Error Messages
  // debugger;
  log(
    "Posting error messages. Waiting for",
    window.location.href.indexOf("/marketing/social-planner/") > 0
      ? "*.hl_topbar-tabs"
      : "*.hl_without-topbar"
  );
  waitForElement(
    window.location.href.indexOf("/marketing/social-planner/") > 0
      ? "*.hl_topbar-tabs"
      : "*.hl_without-topbar"
  ).then((el) => {
    console.log("This should be the element", el);
    // debugger;
    setTimeout(async () => {
      if (!document.getElementById("img_error_root")) {
        let img_error_root = document.createElement("div");
        img_error_root.id = "img_error_root";

        if (el === document.querySelector("*.hl_without-topbar")) {
          log("Same element.", el);
        } else {
          log(
            "Not the same element.",
            document.querySelector("*.hl_without-topbar")
          );
          log("Waiting for element...");

          let selector;
          if (document.querySelector("#socialMediaPlanner")) {
            selector = "*.hl_topbar-tabs";
          } else {
            selector = "*.hl_without-topbar";
          }
          await waitForElement(selector);
          log("Element found", document.querySelector(selector));
          el = document.querySelector(selector);
        }

        if (document.querySelector("*.hl_without-topbar .custom-link")) {
          el = document.querySelector(".hl_wrapper");
          log("Custom link found", document.querySelector(".custom-link"), el);
        }

        log("Rendering Error Root", el);

        el = await waitForElement(".hl_header");
        el.append(img_error_root);

        img_error_root = ReactDOM.createRoot(
          document.getElementById("img_error_root")
        );
        img_error_root.render(
          <Provider store={store}>
            <ErrorMessages />
          </Provider>
        );
      } else {
        log("Error root already rendered");
      }
    }, 1);
  });
}

