async function reload() {
  console.log("Reloaded");
  await waitForElement("#app:not(.loading)");
  waitForElement("#sidebar-v2 > div.h-screen > div > div.hl_nav-header")
    .then((element) => {
      // Make the toggle button
      let switchRoot = document.createElement("div");
      // switchRoot.attachShadow({ mode: "open" });
      switchRoot.id = "switch_root";
      switchRoot.style.marginTop = "-14px";
      switchRoot.style.marginBottom = "6px";
      // Append before...
      document
        .querySelector("#sidebar-v2 > div.h-screen > div > div.hl_nav-header")
        .parentNode.insertBefore(switchRoot, element);

      switchRoot = ReactDOM.createRoot(document.getElementById("switch_root"));
      switchRoot.render(
        <div className="!img-flex !img-flex-col !img-items-center">
          <label className="!img-relative !img-flex !img-justify-between !img-items-center img-group !img-p-2 !img-text-xl">
            <input
              type="checkbox"
              id="img-dark-mode-toggle"
              defaultChecked={false}
              className="!img-cursor-pointer !img-absolute !img-left-1/2 !img--translate-x-1/2 !img-w-full !img-h-full img-peer !img-appearance-none !img-rounded-md"
              onClick={(e) => {
                if (e.target.checked) {
                  document.querySelector("#sb_launchpad > img").src =
                    "https://storage.googleapis.com/highlevel-backend.appspot.com/sidebar-v2/icon_launchpad.svg";
                  document.body.dataset.theme = "dark-mode";
                } else {
                  document.querySelector("#sb_launchpad > img").src =
                    "data:image/png;base64,";
                  document.body.dataset.theme = "default";
                }
                // alert(
                //   "I knew you'd click this! ;) Dark mode is coming soon! We will be performing scheduled maintenance every day from 9:00 PM to 9:00 AM CST daily. Reach out to us if you need help! -Donald"
                // );
              }}
            />
            <span className="!box-border !img-cursor-pointer !img-w-16 !img-h-7 !img-flex !img-items-center !img-flex-shrink-0 !img-p-1 !img-bg-gray-300 !img-rounded-full !img-duration-300 !img-ease-in-out peer-checked:!img-bg-[#544fff] after:!img-w-5 after:!img-h-5 after:!img-bg-white after:!img-rounded-full after:!img-shadow-md after:!img-duration-300 peer-checked:after:!img-translate-x-[175%] group-hover:after:!img-translate-x-1"></span>
          </label>
        </div>
      );
      return element;
    })
    .finally(() => {
      const callback = (mutationList, observer) => {
        reload();
        observer.disconnect();
        document.querySelector("#switch_root").remove();
      };
      const observer = new MutationObserver(callback);
      const target = document.getElementById("app");
      observer.observe(target, { childList: true, subtree: true });
    });
}

reload();

let enableDarkMode = false,
  darkModeIsSet = false;

function checkForNines(firstTime) {
  darkModeIsSet = document.querySelector('body[data-theme="dark-mode"]')
    ? true
    : false;

  console.log("996 Interval called");
  console.log("Dark Mode isSet?", darkModeIsSet);
  var now = new Date();

  if (firstTime) {
    if (now.getHours() >= 22 && now.getHours() <= 24) {
      enableDarkMode = true;
      document.body.dataset.theme = "dark-mode";
    } else if (now.getHours() >= 0 && now.getHours() <= 9) {
      enableDarkMode = true;
      document.body.dataset.theme = "dark-mode";
    } else {
      enableDarkMode = false;
      document.body.dataset.theme = "default";
    }
  }

  if (now.getHours() === 22) {
    if (now.getMinutes() === 0 && !darkModeIsSet) {
      enableDarkMode = true;
      document.body.dataset.theme = "dark-mode";
      document.querySelector("#img-dark-mode-toggle").click();
    }
  } else if (now.getHours() === 9) {
    if (now.getMinutes() === 0 && !darkModeIsSet) {
      enableDarkMode = false;
      document.body.dataset.theme = "default";
    }
  }
}
checkForNines(true);
setInterval(checkForNines, 60000);

